.head {
    width: 100%;
    box-shadow: 0px 1px 40px rgba(51, 51, 51, 0.1);
    margin-bottom: 10px;
    background: linear-gradient(125.95deg, #C700BF 10.95%, #c0adec 100%), linear-gradient(341.1deg, #00ff15 7.52%, #48148b 77.98%), linear-gradient(222.34deg, #900909 12.99%, #00FFE0 87.21%), linear-gradient(130.22deg, #1149ff 18.02%, #ff315e 100%);
background-blend-mode: screen, color-dodge, color-dodge, normal;
}
.row{
    height: 35px;
    display: flex;
    justify-content:space-between;
    align-items: center;

}
.title{
    font-size: 16px;
    color: var(--color-title);
    font-family: 'Montserrat', sans-serif;
}

.logo{
    width: 30px;
    height: 25px;
    background-color: transparent;
    margin-right: 4px;
}

.loginBlock{
    margin-left: auto;
}
.link{
    color: var(--color-title);
    font-family: 'Montserrat', sans-serif;
    margin-right: 6px;
}
ul{
    margin: 0;
    padding: 0;
}
li {
    list-style-type: none;
}
.login_button{
    font-family: 'Montserrat', sans-serif;
    background-color: transparent;
    cursor: pointer;
    border: none;
    font-size: 12px;
    color: var(--color-title);
    padding: 2px 10px;
}

.login_button:hover{
    border-radius: 3px;
    background-color: var(--color-hover);
}

.profileRow{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 35px;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 

}

.profileRow:hover{
    background-color: var(--color-hover);
    cursor: pointer;
    border-radius: 3px;

}

.profileImg{
    margin-top: 3px;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    border: 0;
}

.menuImg{
    width: 10px;
    height: 10px;
    cursor: pointer;
    margin: 0 5px;
}

.menu{
    position: absolute;
    display: none;
    width: 200px;
    background-color: var(--color-primary);
    border-radius: 4px;
    border: 1px solid var(--color-separator);
    box-shadow: 0 1px 3px var(--color-alpha);
    z-index: 1;
}
.active{
    display: block;
}

.menuBlock{
    flex-direction: row-reverse;
    max-width: 1100px;
    margin: 0 auto;
    display: flex;
}