.paginator{
    display: flex;
    justify-content: center;
    padding-left: 10px;
}
.page{
    position: relative;
    padding: 0px 5px ;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
    width: 24px;
    text-align: center;
    margin-right: 2px;
    margin-left: 2px;
    line-height: 22px;
    font-size: 14px;
}

.pageActive{
    color: red;
}